
.project-heading{

   text-align: center; 
   padding: 4rem 0 2rem 0;
}

.project-container2{
  
    max-width: 1140px;
    margin:auto;
    display:grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap:40px;
    padding-bottom: 5rem;

}

.project-card{
    background: #1a1919;
    padding:1.2rem 1rem;
    border:1px solid white;
    display: inline-block;
    animation:bounce; 
    animation-duration: 2s;

}
.project-tool{
  color:orange;
  padding-bottom:0.3rem;
}


.project-card img{
 width:100%;
}

.project-title{
  color:white;
    padding:1rem 0;
}

.project-details p{
    padding-top: 10px;
    padding-bottom:1rem;
    font-size:1.1rem ;
    text-align: justify-center;
  }

.project-btns{
    display: flex;
    padding:0.5rem 0 ;
    justify-content: space-between;
}

.project-btns .Btn{
  padding: 0.5rem 1rem;
}




@media screen and (max-width:740px){
    .project-container2{
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}